body {
    font: 14px "Courier New", Futura, sans-serif;
    font-weight: bold;
    margin: 20px;
    background-color: #f8f3f3;
}

ol,
ul {
    padding-left: 30px;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.status {
    margin-bottom: 10px;
}
.scoreX{
    color: rgb(202, 9, 9);
    font-style: normal;
    font-size: large;
}

.scoreO{
    color: rgb(58, 16, 227);
    font-style: normal;
    font-size: large;
}

.square {
    background: #fff;
    border: 2px solid #999;
    float: left;
    font-size: 15px;
    font-weight: normal;
    line-height: 34px;
    height: 34px;
    /* margin-right: -1px;
    margin-top: -1px; */
    padding: 0;
    text-align: center;
    width: 34px;
    color: rgb(138, 135, 135);
    border-bottom: 3px solid #7B7B7B;
    border-left: 3px solid #c8c8c8;
    border-top: 3px solid #c8c8c8;
    border-right: 3px solid #7B7B7B;
    
}

.blue{
    color: #0b32f3;
}

.red{
    color: #fe0000;
}

.square.empty {
    background: rgb(185, 185, 185);
    border-bottom: 2px solid #7B7B7B;
    border-left: 2px solid #7B7B7B;;
    border-top: 2px solid #7B7B7B;;
    border-right: 2px solid #7B7B7B;
}

