button {
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    border-radius: 4px;
    transition-duration: 0.4s;
    font: 14px "Courier New", Futura, sans-serif;
    font-weight: bold;
  }

  button:hover {
    background-color: #49794b; /* Green */
    color: white;
  }

